    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color:#007bff;
    color: white;
    padding: 10px 20px;
  }
  
  .header-logo a {
    color: white;
    text-decoration: none;
    font-size: 1.5rem;
    font-weight: bold;
  }
  
  .header-actions a {
    color: white;
    text-decoration: none;
    margin-left: 20px;
    font-size: 1rem;
  }
  
  .header-actions a:hover {
    text-decoration: underline;
  }
  
  
  .car-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 20px;
    padding: 20px;
  }
  
  .car-card {
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 16px;
    text-align: center;
  }
  
  .car-card img {
    max-width: 100%;
    height: auto;
    border-radius: 4px;
  }
  
  .car-card button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 8px 16px;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .car-card button:hover {
    background-color: #0056b3;
  }
  
  .cart {
    padding: 20px;
  }
  
  .cart ul {
    list-style: none;
    padding: 0;
  }
  
  .cart li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
  }
  
  .cart li img {
    border-radius: 4px;
  }
  .cart-item-enter {
    opacity: 0;
  }
  
  .cart-item-enter-active {
    opacity: 1;
    transition: opacity 300ms;
  }
  
  .cart-item-exit {
