import React from 'react';
import { useSelector } from 'react-redux';
import { Button, Snackbar } from '@mui/material';

const CarCard = ({ car, onAddToCart }) => {
  const user = useSelector((state) => state.auth.user); //     
  const [openSnackbar, setOpenSnackbar] = React.useState(false);

  const handleAddToCart = () => {
    if (user) {
      onAddToCart(user.id, car); //  user.id  car  
      setOpenSnackbar(true); //  
    }
  };

  const handleCloseSnackbar = () => {
    setOpenSnackbar(false); //  
  };

  return (
    <div className="car-card">
      <img src={car.image} alt={car.model} width={200} />
      <h3>{car.brand} {car.model}</h3>
      <p> : {car.year || ' '}</p>
      <p>: ${car.price ? car.price.toLocaleString() : ' '}</p>
      <p> : {car.type || ' '}</p>
      <p>: {car.mileage ? `${car.mileage.toLocaleString()} ` : ' '}</p>
      <p>{car.description || ''}</p>
      {user ? (
        <Button variant="contained" color="primary" onClick={handleAddToCart}>
          Add to Cart
        </Button>
      ) : (
        <p style={{ color: 'red' }}>You must be logged in to add to cart</p>
      )}

      <Snackbar
        open={openSnackbar}
        autoHideDuration={3000}
        onClose={handleCloseSnackbar}
        message="    !"
      />
    </div>
  );
};

export default CarCard;
